import { WebComponent } from "../../decorators";
import { VetproviehBasicRepeat, VetproviehElement } from "../../core";
import { Indexable } from "../../interfaces";
import { ViewHelper } from "../../helpers";


@WebComponent({
    template: VetproviehElement.template + `
        <table class="table">
          <thead>
          </thead>
          <tbody id="listElements">
          </tbody>
        </table>
    `,
    tag: "vp-table"
})
export class VetproviehTable extends VetproviehBasicRepeat {


    /**
     * Inserts Element to List
     * @param {any} dataItem
     * @param {index} number
     * @private
     */
    protected _attachToList(dataItem: any, index = 0) {
      if (this.shadowRoot) {
        const newListItem: HTMLElement = this._generateListItem(dataItem);
        dataItem['index'] = index;
        ViewHelper.replacePlaceholders(newListItem, dataItem);

        this._attachParamsToLink(newListItem, dataItem);

        const list = this.list;
        if (list) {
          list.appendChild(newListItem.children[0]);
        }
      }
    }

    private _attachParamsToLink(newListItem: HTMLElement,dataItem: any){
      let a = newListItem.getElementsByTagName("a")[0];
      if(a){
        (a as any).params = dataItem;
      }
    }
    
     /**
    * Generate new Item for List which is based on the template
    * @param {any} dataItem
    * @param {boolean} activatedEventListener
    * @return {HTMLDivElement}
    * @private
    */
   protected _generateListItem(dataItem: any, activatedEventListener = false) {
    const newNode = document.importNode(this._listTemplate, true);
    const div = document.createElement('tbody');

    div.appendChild(newNode);

    return div;
  }
}